/* src/pages/NotFound.css */
.not-found {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - 70px);
    background-color: #f8f9fa;
}

.error-container {
    max-width: 500px;
    padding: 3rem;
    background-color: white;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.error-code {
    font-size: 6rem;
    font-weight: 700;
    color: #6c757d;
    line-height: 1;
    margin-bottom: 1rem;
}

.error-container h1 {
    margin: 0 0 1rem 0;
    color: #343a40;
    font-size: 2rem;
}

.error-container p {
    color: #6c757d;
    margin-bottom: 2rem;
}

.action-buttons {
    margin-top: 2rem;
}

.primary-button {
    display: inline-block;
    padding: 0.75rem 1.5rem;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 600;
    transition: background-color 0.2s;
}

.primary-button:hover {
    background-color: #0069d9;
}